import React from 'react';
import { CSSTransition, } from 'react-transition-group';
import { CSSTransitionProps } from 'react-transition-group/CSSTransition';
import { makeStyles } from '@material-ui/core/styles';

export default function CircularReveal(props: CSSTransitionProps) {


   const drawCircle = function(radius: string)
  {
    const transform = 'circle(' + radius + ' at ' + props.style.transformOrigin + ")";
    return transform;
  } 

  const animationTime = function()
  {
    const time = "clip-path " + props.timeout + "ms";
    return time;
  } 

  const useStyles = makeStyles({
    contentEnter: {
      clipPath: drawCircle("0%"),
    },
    contentEnterActive: {
      clipPath: drawCircle("200%"),
      transition: animationTime(),
    },
    contentExit: {
      clipPath: drawCircle("200%"),
    },
    contentExitActive: {
      clipPath: drawCircle("0%"),
      transition: animationTime(),
    },
  });

  const classes = useStyles();
  return (
    <CSSTransition
      in={props.in}
      timeout={props.timeout}
      unmountOnExit
      classNames={{
        enter: classes.contentEnter,
        enterActive: classes.contentEnterActive,
        exit: classes.contentExit,
        exitActive: classes.contentExitActive,
      }}
      addEndListener={()=>null}>
      {props.children}
    </CSSTransition>
  );
}

