import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import axios from 'axios'
import ImageGallery from './components/ImageGallery';
import { Grid, useMediaQuery } from '@material-ui/core';
import ProjectInfo from './components/ProjectInfo';

const useStyles = makeStyles((theme) => ({
    project_content_wrapper: {
        background: theme.palette.background.default,
        width: "100%",
        height: "100%",
        left: 0,
        top: "48px",
        position: "fixed",
    },
    project_content: {
        padding: "24px",
        height: "calc(100% - 24px)",
    },
    project_container:
    {
        height: "calc(100% - 48px)",
    },
    background_container_landscape:
    {
        height: "100%",
    },
    background_container:
    {
        height: "50%",
    },
    close_project_btn:
    {
        float: "right",
        zIndex: 1000,
        position: "absolute",
        right: "0px",
    },
    project_title:
    {
        maxWidth: "80%",
    },
    image_gallery:
    {
        height: "100%"
    },
    info_container_landscape:
    {
        height: "100%",
        position: "relative",
        overflowY: "auto"
    },
    info_container:
    {
        height: "50%",
        position: "relative",
        overflowY: "auto"
    }
}));

export default function Project(props: any) {
    const classes = useStyles();
    const [projectData, setProjectData] = React.useState<{ [x: string]: any }>({});
    const isLandscape = useMediaQuery('(min-width: 600px)');

    useEffect(() => {
        const fetchData = async () => {
            const axiosInstance = axios.create({ baseURL: process.env.PUBLIC_URL });
            axiosInstance.get("/project_data/" + props.projectPath + "/description.json")
                .then(async function (response: { [x: string]: any }) {
                    if (response.data !== "")
                        setProjectData(response.data);
                    else
                        setProjectData({ "ProjectName": "No info available" })
                })
                .catch(function (error: any) {
                    // handle error
                    console.log(error);
                })
                .finally(function () {
                    // always executed
                });
        };
        fetchData();
        document.body.style.overflow = "hidden";
    }, [props.projectPath]);


    const getProjectDataArray = function (key: string) {
        if (typeof projectData !== "undefined" && projectData !== null && key in projectData)
            return projectData[key];
        return [];
    }

    return (
        <div className={classes.project_content_wrapper}>
            <div className={classes.project_content}>
            <IconButton
             color="secondary" className={classes.close_project_btn} onClick={(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => { props.closeHandler(event, "hidden") }}>
                <CloseIcon></CloseIcon>
            </IconButton>
            <Grid className={classes.project_container} container spacing={1} alignItems="stretch">
                <Grid className={isLandscape ? classes.background_container_landscape : classes.background_container} item xs={12} sm={8}>
                    <ImageGallery className={classes.image_gallery} media={getProjectDataArray("Media")}></ImageGallery>
                </Grid>
                <Grid className={isLandscape ? classes.info_container_landscape : classes.info_container}  item xs={12} sm={4}>
                    <ProjectInfo  info={projectData}></ProjectInfo>
                </Grid>
            </Grid>
            </div>
        </div>
    );
}
