import React, { useEffect, useCallback } from 'react';
import { makeStyles } from '@material-ui/core';
import { Pagination, Skeleton } from '@material-ui/lab'
import { Swipeable, EventData } from 'react-swipeable'


export default function ImageGallery(props: any) {
    const [currentMedia, setCurrentMedia] = React.useState<number>(0);
    const [loading, setLoading] = React.useState<boolean>(true);
    const handleChangeMedia = function (event: React.ChangeEvent<unknown>, page: number) {
        setCurrentMedia(page - 1);
    }

    const getCurrentImage = function () {
        return "url(\"" + process.env.PUBLIC_URL + "/" + props.media[currentMedia] + "\");";
    }

    const nextMedia = useCallback(() => {
        if (currentMedia < props.media.length - 1)
            setCurrentMedia(currentMedia + 1)
    }, [currentMedia, props.media.length])

    const prevMedia = useCallback(() => {
        if (currentMedia > 0)
            setCurrentMedia(currentMedia - 1)
    }, [currentMedia])

    const onSwipeEvent = (eventData: EventData) => {
        if (eventData.deltaX > 0)
            nextMedia()
        else if (eventData.deltaX < 0)
            prevMedia()
    }


    const useStyles = makeStyles({
        background_image:
        {
            backgroundSize: "contain",
            background: getCurrentImage(),
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            width: "100%",
            height: "100%",
            position: "relative"
        },
        background_image_wrapper:
        {
            width: "100%",
            height: "100%",
        },
        gallery_pagination:
        {
            marginTop: "12px",
            display: "flex",
            justifyContent: "center",
        },
        inner_background_image_wrapper:
        {
            width: "100%",
            height: "100%",
        },
        swippable_wrapper:
        {
            width: "100%",
            height: "calc(100% - 48px)",
        }
    });

    const classes = useStyles();

    useEffect(() => {
        const nextHandler = function (event: KeyboardEvent) {
            if (event.key === "ArrowLeft") {
                prevMedia();
            }
    
            else if (event.key === "ArrowRight") {
                nextMedia();
            }
        };
        
        if (typeof props.media !== "undefined" && props.media.length > 0) {
            setLoading(false);
            window.addEventListener("keydown", nextHandler);
            return () => {
                window.removeEventListener("keydown", nextHandler);
            };
        }
        else {
            setLoading(true);
        }
    }, [nextMedia, prevMedia, currentMedia, props]);

    return (
        <div className={classes.background_image_wrapper}>
            {
                loading ? <Skeleton /> :
                    <div className={classes.inner_background_image_wrapper}>
                        <Swipeable className={classes.swippable_wrapper} onSwiped={(eventData) => onSwipeEvent(eventData)} >
                            <div className={classes.background_image}></div>
                        </Swipeable>
                        <Pagination page={currentMedia + 1} className={classes.gallery_pagination} color="secondary" count={props.media.length} onChange={(event, page) => handleChangeMedia(event, page)} size="small" />
                    </div>
            }
        </div >
    );
}



