import React from 'react';
import { Typography, makeStyles, Grid, useMediaQuery } from '@material-ui/core';

const useStyles = makeStyles({
    place:
    {
        alignSelf: "flex-end",
        marginBottom: "0px",
        flex: 0.55,
    },
    year: {
        minWidth: "75px",
        verticalAlign: "bottom",
    },
    year_portrait: {
        minWidth: "75px",
        verticalAlign: "bottom",
        marginBottom: "-10px"
    },
    top_info: {
        display: "flex",
        flexDirection: "row"
    },
    top_info_portrait:{
        display: "flex",
        flexDirection: "column"
    },
    position:
    {
        alignSelf: "flex-end",
        marginBottom: "0px",
        flex: 1        
    },
    position_portrait:
    {
        marginBottom: "0px",
        flex: 1,
        alignSelf: "flex-start"
    },
    place_portrait:
    {
        marginBottom: "0px",
        alignSelf: "flex-start"
    }
});

export default function CVEntry(props: any) {
    const classes = useStyles();
    const isLandscape = useMediaQuery('(min-width: 600px)');

    return (
        <Grid container direction="row" justify="center" spacing={1}>
            <Grid item xs={12} sm={2}>
                <Typography className={isLandscape ? classes.year : classes.year_portrait} gutterBottom variant="button" component="div">
                    {props.year}
                </Typography>
            </Grid>
            <Grid item xs={12} sm={10}>
                <div>
                    <div className={isLandscape ? classes.top_info : classes.top_info_portrait}>
                        <Typography className={isLandscape ? classes.position : classes.position_portrait}  gutterBottom variant="h6" component="div" >
                            {props.position}
                        </Typography>
                        <Typography className={isLandscape ? classes.place : classes.place_portrait} gutterBottom variant="caption" component="div" align="right">
                            {props.place}
                        </Typography>
                    </div>
                    <Typography color="textSecondary" gutterBottom variant="body2" component="div" align="justify">
                        {props.children}
                    </Typography>
                </div>
            </Grid>

        </Grid>
    );
}



