import { AxiosInstance } from "axios";

class GithubFm {
    starting_url: string;
    axiosInstance: AxiosInstance;
    project_folders: any;
    
    constructor(public newAxiosInstance : AxiosInstance)
    {
        this.axiosInstance = newAxiosInstance;
        this.starting_url = process.env.PUBLIC_URL + "/project_data/fileStructure.json";
        this.project_folders = {};
    }

    async getFileStructure()
    {
        const currentInstance = this;
        await this.axiosInstance.get(this.starting_url)
        .then(async function (response: any) {
            currentInstance.project_folders = response.data;
        })
        .catch(function (error: any) {
            // handle error
            console.log(error);
        })
        .finally(function () {
            // always executed
        });
    }
}

export default GithubFm;

  