import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Portfolio from './portfolio';
import CV from './cv';
import { IconButton } from '@material-ui/core';
import WbSunnyIcon from '@material-ui/icons/WbSunny';
import NightsStayIcon from '@material-ui/icons/NightsStay';
import Contacts from './contacts';
import { Switch, Route, Link, useLocation, RouteProps } from 'react-router-dom';
import NotFound from './NotFound';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  tabs:
  {
    flexGrow: 1,
  },
  tab_content:
  {
    paddingTop: "72px",
    backgroundColor: theme.palette.background.default,
    overflow: "auto"
  },
  theme_switch:
  {
    marginRight: "24px",
  },
  app_bar: {
    display: "flex",
    flexDirection: "row"
  },
  theme_icon: {
    verticalAlign: "middle"
  },
}));

export default function NavigationBar(props: any) {
  const classes = useStyles();
  const location: RouteProps["location"] = useLocation();

  const a11yProps = function(index: string) {
    if (!location.pathname.includes("university/") && !location.pathname.includes("personal/") )
    document.body.style.overflow = "overlay";
    return {
      id: `scrollable-auto-tab-${index}`,
      'aria-controls': `scrollable-auto-tabpanel-${index}`,
    };
  }

  return (
    <div className={classes.root}>
      <AppBar className={classes.app_bar} position="fixed">
        <Tabs className={classes.tabs} value={"/"+location.pathname.split("/")[1]} aria-label="simple tabs example" variant="scrollable" scrollButtons="on">
          <Tab component={Link} to={"/"} value={"/"} label="CV" {...a11yProps(location.pathname)} />
          <Tab component={Link} to={'/university'} value={"/university"} label="University" {...a11yProps(location.pathname)} />
          <Tab component={Link} to={'/personal'} value={"/personal"} label="Personal" {...a11yProps(location.pathname)} />
          <Tab component={Link} to={'/contacts'} value={"/contacts"} label="Contacts" {...a11yProps(location.pathname)} />
        </Tabs>
        <div className={classes.theme_switch}>
          <IconButton onClick={() => props.handleThemeChanged()} aria-label="change theme" color="secondary">
            {props.darkModeActive ? <WbSunnyIcon /> : <NightsStayIcon />}
          </IconButton>
        </div>
      </AppBar>
      <Switch>
        <Route exact path="/">
          <div className={classes.tab_content}>
            <CV></CV>
          </div>
        </Route>
        <Route path="/university">
          <div className={classes.tab_content}>
            <Portfolio projectType="university" portfolioData={props.portfolioData}></Portfolio>
          </div>
        </Route>
        <Route path="/personal">
          <div className={classes.tab_content}>
            <Portfolio projectType="personal" portfolioData={props.portfolioData}></Portfolio>
          </div>
        </Route>
        <Route exact path="/contacts">
          <div className={classes.tab_content}>
            <Contacts></Contacts>
          </div>
        </Route>
        <Route>
          <div className={classes.tab_content}>
            <NotFound></NotFound>
          </div>
        </Route>
      </Switch>
    </div>
  );
}
