import React, { useEffect, useState } from 'react';
import axios from 'axios'
import GithubFm from './github-fm'
import NavigationBar from './navigation-bar'
import './App.css';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import { colors } from '@material-ui/core';
import { HashRouter } from 'react-router-dom';

function App() {
  const [isDarkModeActive, setIsDarkModeActive] = React.useState(localStorage.getItem('darkMode') === "true");
  const [data, setData] = useState({});

  const normalTheme = createMuiTheme({
    palette: {
      primary: colors.lime,
      secondary: colors.deepOrange
    },
  });
  const darkTheme = createMuiTheme({
    palette: {
      type: 'dark',
      primary: colors.blueGrey
    },
  });

  useEffect(() => {
    const fetchData = async () => {
      const axiosInstance = axios.create({ baseURL: process.env.PUBLIC_URL });
      const githubfm = new GithubFm(axiosInstance);
      await githubfm.getFileStructure();
      setData(githubfm.project_folders);
    };
    fetchData();
    document.body.style.overflow = "overlay";
  }, []); // notice the empty array here, this is optional


  const handleThemeChange = function () {
    const currentDarkMode = isDarkModeActive;
    setIsDarkModeActive(!currentDarkMode);
    localStorage.setItem('darkMode', (!currentDarkMode).toString());
   // changeTheme(!currentDarkMode);
  };

  return (
    <HashRouter basename='/'>
      <ThemeProvider theme={isDarkModeActive ? darkTheme : normalTheme}>
        <NavigationBar darkModeActive={isDarkModeActive} handleThemeChanged={handleThemeChange} portfolioData={data} />
      </ThemeProvider>
    </HashRouter>
  );
}

export default App;
