import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import CVEntry from './components/CVEntry';
import { Grid, Fab, Link, useMediaQuery } from '@material-ui/core';
import CompetenceEntry from './components/CompetenceEntry';
import GetAppIcon from '@material-ui/icons/GetApp';


const useStyles = makeStyles({
    competences: {
        marginTop: "24px",
        maxWidth: "900px;",
        marginLeft: "auto",
        marginRight: "auto"
    },
    profile_info:
    {
        marginTop: "24px",
        display: "flex",
        alignItems: "center",
    },
    profile_info_text:
    {
        marginLeft: "24px",
        marginRight: "auto"
    },
    profile_info_img:
    {
        width: "130px",
        marginLeft: "auto",
    },
    fab: {
        position: "fixed",
        bottom: "24px",
        right: "24px",
    },
    main_container: {
        paddingBottom: "48px",
    },
    horizontal_line: {
        visibility: "hidden"
    },
    horizontal_line_portrait: {
        visibility: "visible"
    }
});

export default function CV() {
    const classes = useStyles();
    const maxCompentence = 3;
    const isLandscape = useMediaQuery('(min-width: 600px)');

    return (
        <div className={classes.main_container}>
            <div className={classes.profile_info}>
                <img className={classes.profile_info_img} alt="profile" src="photo_cv.png"></img>
            <div className={classes.profile_info_text}>
                    <Typography color="textPrimary" variant="h4" component="div">
                        Artur Gomes
                    </Typography>
                    <Typography color="textPrimary" variant="h5" component="div">
                        C++ Software Engineer
                    </Typography>
            </div>
            </div>
            <Card className={classes.competences}>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                        Profile
                    </Typography>
                    <Typography variant="body1" color="textSecondary" component="div">
                        Skilled and passionate software engineer with proven experience in C++, Java and Python.<br/>
                        Been catching up with some of the current industry C++ standards (mostly 11 and 14) and reviewing some of the fundamentals in Data Structures and Algorithms, while preparing for the next position.
                    </Typography>
                </CardContent>
            </Card>
            <Card className={classes.competences}>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                        Competences
                    </Typography>
                    <Grid container alignItems="center" direction="row" justify="flex-start" spacing={3}>
                    <CompetenceEntry value={3} max={maxCompentence}>C++</CompetenceEntry>
                    <CompetenceEntry value={3} max={maxCompentence}>Java</CompetenceEntry>
                    <CompetenceEntry value={3} max={maxCompentence}>Python</CompetenceEntry>
                    <CompetenceEntry value={1} max={maxCompentence}>Unreal Engine 4</CompetenceEntry>
                    <CompetenceEntry value={0.5} max={maxCompentence} experienceText={"6 months"}>C</CompetenceEntry>
                    <CompetenceEntry value={2} max={maxCompentence}>Android</CompetenceEntry>
                    <CompetenceEntry value={2} max={maxCompentence}>Javascript</CompetenceEntry>
                    <CompetenceEntry value={1} max={maxCompentence}>PHP</CompetenceEntry>
                    <CompetenceEntry value={2} max={maxCompentence}>CSS</CompetenceEntry>
                    <CompetenceEntry value={2} max={maxCompentence}>HTML</CompetenceEntry>
                    <CompetenceEntry value={2} max={maxCompentence}>SQL</CompetenceEntry>
                    </Grid>
                </CardContent>
            </Card>
            <Card className={classes.competences}>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                        Experience
                    </Typography>
                    <CVEntry
                        year="2018-2020"
                        place={"Amadeus/Astek"}
                        position={"Software Engineer C++"}>
                        Maintained and developed several mission critical C++ backend applications as a member of the Reservation and Departure Control synchronization team while contracted to Amadeus via Astek.<br />Highly contributed to the drastic reduction in the number of production issues (bugs, core dumps and memory leaks) from over 200 to around 20.<br />Fixed issues that had been in the backlog for many years.<br />Automated repetitive and recovery tasks using Python.<br />Reduced the runtime of tests of all our components by up to 75%.
                    </CVEntry>
                    <hr className={isLandscape ? classes.horizontal_line : classes.horizontal_line_portrait}></hr>
                    <CVEntry
                        year="2017-2018"
                        place={"Mimicry Games, Lda"}
                        position={"Software Engineer, Games and Virtual Reality"}>
                        Developed FingerPaint application for High Fidelity.<br />Worked on company VR project Ganbatte using Unreal Engine 4 which was released on the Steam platform after 9 months of development.<br />Successfully created all the single player levels, menus and websites for Ganbatte.
                    </CVEntry>
                    <hr className={isLandscape ? classes.horizontal_line : classes.horizontal_line_portrait}></hr>
                    <CVEntry
                        year="2015-2017"
                        place={"Consipere - Software Lda"}
                        position={"Software Engineer"}>
                        Worked on the full stack of the company, first as an Android developer then on server side using python and finally developed some firmware for sensors.<br />Reduced synchronization time of between the android application and server by over 50%.<br />Developed firmware for FemtoIO sensors using C++ for Arduino, which would collect data. This data would then be processed by Machine Learning algorithms to infer the type of movement applied to that sensor.<br />Re-purposed some pre-trained Machine Learning models and developed some tools to monitor the training using Python.
                    </CVEntry>


                </CardContent>
            </Card>
            <Card className={classes.competences}>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                        Education
                    </Typography>
                    <CVEntry
                        year="2014-2014"
                        place={"Faculty of Letters, University of Porto"}
                        position={"Intensive German Course - Level A1.1"}>
                        Learned the basics of German Language during a summer intensive course.Achieved a grade of 17 out of 20.
                    </CVEntry>
                    <hr className={isLandscape ? classes.horizontal_line : classes.horizontal_line_portrait}></hr>
                    <CVEntry
                        year="2009-2014"
                        place={"Faculty of Engineering, University of Porto"}
                        position={"Masters in Informatics and Computing Engineering"}>
                        Learned and developed programming skills and knowledge on Software Engineering.<br />Completed with grade 14 out of 20.<br />Wrote dissertation Procedural Generation Based on Affective Data which aimed to help game designers use procedural generation techniques to modify the gamers affective state.
                    </CVEntry>
                    <hr className={isLandscape ? classes.horizontal_line : classes.horizontal_line_portrait}></hr>
                    <CVEntry
                        year="2013-2013"
                        place={"Faculty of Engineering and Architecture, Ghent University"}
                        position={"Erasmus - 1 Semester"}>
                        Information Security, Information Theory, Introduction to Operations Research, Software Design Project.
                    </CVEntry>
                    <hr className={isLandscape ? classes.horizontal_line : classes.horizontal_line_portrait}></hr>
                    <CVEntry
                        year="1999-2009"
                        place={"Academia de Lnguas de Madeira (ALM)"}
                        position={"English Learning"}>
                        Certificate in Advanced English (CAE) from Cambridge.
                    </CVEntry>
                </CardContent>
            </Card>
            <Link href="agomesCV.pdf">                
                <Fab className={classes.fab} color="secondary" aria-label="add">
                    <GetAppIcon />
                </Fab>
            </Link>
        </div>
    );
}
