import React, { useEffect } from 'react';
import axios from 'axios'
import root from 'react-shadow';
import { Skeleton } from '@material-ui/lab';

export default function AnimationIcon(props: { src: string; className: string | undefined; }) {
    const [animation, setAnimation] = React.useState("");
    const [isLoaded, setIsLOaded] = React.useState(false);

    useEffect(() => {
        if (props.src.endsWith(".svg"))
        {
            const fetchData = async () => {
                const axiosInstance = axios.create({ baseURL: process.env.PUBLIC_URL });
                axiosInstance.get(props.src)
                    .then(async function (response: { [x: string]: any }) {
                        if (response.data !== "")
                            setAnimation(response.data);
                    })
                    .catch(function (error: any) {
                        // handle error
                        console.log(error);
                    })
                    .finally(function () {
                        // always executed
                        setIsLOaded(true);
                    });
            };
            setIsLOaded(false);
            fetchData();
        }        
    }, [props.src]);

    return (
        <div className={props.className}>
        {
            isLoaded 
            ? <root.div><div className={props.className} dangerouslySetInnerHTML={{__html: animation}}></div></root.div>
            : <Skeleton  variant="rect"  width="100%" height="100%" animation="wave"></Skeleton>
        }
        </div> 
    );
}



