import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CircularReveal from './transitions/CircularReveal/CircularReveal';
import Project from './Project';
import ProjectTimeline from './components/ProjectTimeline';
import { Route, useRouteMatch, useHistory } from 'react-router-dom';



const useStyles = makeStyles((theme) => ({
    portfolio: {
        position: "relative",
    },
    project_content: {
        zIndex: 999,
        position: "absolute",
        width: "100%",
        height: "100%",
        left: 0,
        top: 0
    },
    close_project_btn:
    {
        float: "right"
    },
    portfolio_icon:
    {
        color: theme.palette.text.primary,
        height: "48px",
        width: "48px"
    },
    portfolio_title_container:{
        display: "flex",
        marginLeft: "24px"
    },
    portfolio_title: {
        marginLeft: "24px"
    },
    portfolio_timeline: {
        marginLeft: "24px"
    }
}));


export default function Portfolio(props: any) {
    const classes = useStyles();
    const history = useHistory();


    let match = useRouteMatch({
        path: '/' + props.projectType + '/:project_path',
        strict: true,
        sensitive: true
    });

    const getPathFromUrl = function () {
        if (match !== null && "project_path" in match.params) {
            const url_project_path: string = match.params["project_path"]
            return Buffer.from(url_project_path, 'base64').toString('ascii')
        }
        return "";
    }
    const [projectPath, setProjectPath] = React.useState(getPathFromUrl());
    const [projectVisibility, setProjectVisibility] = React.useState(getPathFromUrl() !== "" ? "block" : "none");
    const [projectClickPos, setProjectClickPos] = React.useState([-1, -1]);

    const showProject = function (event: React.MouseEvent<HTMLButtonElement, MouseEvent>, newProjectPath: string) {
        setProjectPath(newProjectPath);
        setProjectClickPos([event.clientX, event.clientY-48]);
        setProjectVisibility("block");
    }

    const handleProjectVisibility = (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>, value: React.SetStateAction<string>) => {
        document.body.style.overflow = "overlay";
        setProjectVisibility(value);
        history.push("/"+props.projectType);
    };

    useEffect(() => {
        if (projectVisibility === "block")
        {
            document.body.style.overflow = "hidden";
        }
    }, [projectVisibility]);

    const getProjectClickPosition = function () {
        let s = "";
        if (projectClickPos[0] === -1 && projectClickPos[1] === -1)
            s = "50% 50%";
        else
            s = projectClickPos[0] + 'px ' + projectClickPos[1] + 'px';
        return s;
    }

    return (
        <div className={classes.portfolio}>
            <ProjectTimeline className={classes.portfolio_timeline} showProject={showProject} projectType={props.projectType} timelineData={props.portfolioData[props.projectType]}>
            </ProjectTimeline>
            <Route path={"/" + props.projectType + "/:project_path"}>
            {({ match }) => (
                <CircularReveal in={projectVisibility === "block" && match != null}
                    timeout={500}
                    style={{ transformOrigin: getProjectClickPosition() }}>
                    
                        <Project className={classes.project_content}
                            closeHandler={handleProjectVisibility}
                            projectPath={projectPath} />
                </CircularReveal>
             )}
            </Route>
        </div>
    );
}
