import React from 'react';
import { Avatar, Typography, makeStyles, Chip, Button, Link, ButtonGroup } from '@material-ui/core';
import { Skeleton } from '@material-ui/lab'

const useStyles = makeStyles({
    avatar: {
        display: "flex",
        margin: "12px"
    },
    author_name:
    {
        marginTop: "auto",
        marginBottom: "auto",
        marginLeft: "12px"
    },
    tag:
    {
        marginTop: "12px",
        marginLeft: "12px"
    },
    description:
    {
        marginTop: "24px",
        marginLeft: "12px"
    },
    url:
    {
        marginTop: "24px",
        marginLeft: "12px"
    },
    project_name:
    {
        marginLeft: "12px",
    }
});

export default function ProjectInfo(props: any) {
    const classes = useStyles();

    const description = function () {
        const rows: JSX.Element[] = [];
        if ("Description" in props.info) {
            rows.push(
                <Typography key={0} color="textPrimary" className={classes.description} variant="body1" component="div">
                    {props.info.Description}
                </Typography>
            );

        }
        return rows;
    }

    const urls = function () {
        const rows: JSX.Element[] = [];
        if ("URL" in props.info) {
            const urlNames : string[] = Object.keys(props.info["URL"]);
            const urlLinks = props.info["URL"];
            for (let i = 0; i < urlNames.length; i++) {
                rows.push(
                    <Button key={i}>
                        <Link color="inherit" href={urlLinks[urlNames[i]]} variant="body2">
                        {urlNames[i]}
                        </Link>
                    </Button>
                    
                );
            }
        }
        if (rows.length === 0)
            return null;
        
        return <ButtonGroup disableElevation className={classes.url} variant="contained" color="secondary" aria-label="contained primary button group">{rows}</ButtonGroup>;
    }

    const tags = function () {
        const rows: JSX.Element[] = [];
        if ("Tags" in props.info) {
            const tags = props.info["Tags"];
            for (let i = 0; i < tags.length; i++) {
                rows.push(
                    <Chip key={i} className={classes.tag} label={tags[i]} variant="outlined" />
                );
            }
        }
        return rows;
    }

    const authors = function () {
        const rows: JSX.Element[] = [];
        if ("Authors" in props.info) {
            const authors = props.info["Authors"];
            for (let i = 0; i < authors.length; i++) {
                if (authors[i] === "Artur Gomes") {
                    rows.push(
                        <div key={authors[i]} className={classes.avatar}>
                            <Avatar alt={authors[i]} src="photo_cv.png" />
                            <Typography color="textPrimary" className={classes.author_name} variant="subtitle2" component="span">
                                {authors[i]}
                            </Typography>
                        </div>
                    );
                }
                else {
                    rows.push(
                        <div key={authors[i]} className={classes.avatar}>
                            <Avatar alt={authors[i]} src="">
                                {authors[i][0]}
                            </Avatar>
                            <Typography color="textPrimary" className={classes.author_name} variant="subtitle2" component="div">
                                {authors[i]}
                            </Typography>
                        </div>
                    );
                }
            }
        }
        return rows;
    }

    const getProjectField = function (key: string) {
        return typeof props.info !== "undefined" && props.info !== null && key in props.info;
    }


    return (
        <div>
            <Typography color="textPrimary" className={classes.project_name} variant="h4" component="div">
                {getProjectField("ProjectName") ? props.info.ProjectName : <Skeleton />}
            </Typography>
            {authors()}
            <div>
                {tags()}
            </div>
            {urls()}
            {description()}
        </div >
    );
}



