import React from 'react';
import { Typography, makeStyles } from '@material-ui/core';
import ProjectPreview from './ProjectPreview';

const useStyles = makeStyles({
    year_content:
    {
        display: "flex",
        flexWrap: "wrap"
    },
    opposite_content:
    {
        flex: 0
    },
    year_container:
    {
       marginBottom: "72px",
    }
});

export default function ProjectTimeline(props: any) {

    const classes = useStyles();

    const yearContent = function (year: string) {
        const rows: JSX.Element[] = [];
        const projects = Object.keys(props.timelineData[year]);
        const projectsData = props.timelineData[year];
        let i = 0;
        projects.forEach(project => {
            i++;
            rows.push(<ProjectPreview key={i} showProject={props.showProject} projectPath={props.projectType+"/"+year+"/"+project} projectName={project} previewImage={projectsData[project].PreviewImage}  projectType={props.projectType}></ProjectPreview>);            
        });
        return rows;
    }

    const years = function () {
        const rows: JSX.Element[] = [];
        if (props.timelineData) {
            const years = Object.keys(props.timelineData);
            for (let i = 0; i < years.length; i++) {
                rows.push(
                    <div key={i}  className={classes.year_container}>
                            <Typography variant="h4" color="textSecondary">
                                {years[i]}
                            </Typography>
                            
                            <div className={classes.year_content}>
                            {yearContent(years[i])}
                            </div>
                    </div >
                );
            }
        }
        return rows;
    }

    return (
        <div className={props.className}>
            {years()}
        </div >
    );
}



