import React from 'react';
import { Typography, makeStyles, Card, CardMedia, CardActions, CardActionArea } from '@material-ui/core';
import AnimationIcon from './AnimatedIcon';
import { Link } from 'react-router-dom';

export default function ProjectPreview(props: { showProject: (arg0: React.MouseEvent<HTMLAnchorElement, MouseEvent>, arg1: any) => void; projectPath: string; projectType: string; previewImage: string | null | undefined; projectName: React.ReactNode; }) {
    const icon = "url(\"icon.html\")";

    const useStyles = makeStyles((theme) => ({
        preview_card: {
            marginRight: "12px",
            marginBottom: "12px",
            width: "260px",
            cursor: ""
        },
        media: {
            width: "260px",
            height: "260px",
            backgroundColor: theme.palette.action.disabledBackground
        },
        icon:
        {
            '& path': {
                fill: "red"
            },
            width: "100%",
            height: "260px",
            fill: "red",
        },
        preview_image:
        {
            mask: icon,
            backgroundColor: "red",
            maskRepeat: "no-repeat",
            maskPosition: "center",
            maskSize: "260px",
            width: "100%",
            height: "100%"
        },
        animated_preview:
        {
            width: "100%",
            height: "260px",
            overflow: "hidden"
        }
    }));

    const classes = useStyles();

    const projectClicked = function (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) {
        props.showProject(event, props.projectPath);
    }

    const getMedia = function () {
        if (props.previewImage !== null && props.previewImage !== undefined && props.previewImage.endsWith(".svg")) {
            return <CardMedia
                className={classes.media}>
                <AnimationIcon src={process.env.PUBLIC_URL + "/"+ props.previewImage} className={classes.animated_preview}></AnimationIcon>
            </CardMedia>;
        }
        else {
            return <CardMedia
                className={classes.media}
                image={process.env.PUBLIC_URL + "/" + props.previewImage}>
            </CardMedia>;
        }
    }

    return (
        <Card className={classes.preview_card}>
            <CardActionArea component={Link} to={"/"+ props.projectType + "/" + new Buffer(props.projectPath).toString('base64')} 
            onClick={(event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => projectClicked(event)}>
                {getMedia()}
                <CardActions>
                    <Typography variant="subtitle1" color="textSecondary" component="p">
                        {props.projectName}
                    </Typography>
                </CardActions>
            </CardActionArea>
        </Card>
    );
}



