import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { Link, List, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import AlternateEmailIcon from '@material-ui/icons/AlternateEmail';

const useStyles = makeStyles({
    contacts: {
        margin: "auto",
    },
    link:
    {

    },
    contacts_wrapper: {
        width: "100%",
        height: "calc(100vh - 24px)",
        marginTop: "-48px",
        display: "flex",
    },
});

export default function Contacts() {
    const classes = useStyles();

    return (
        <div className={classes.contacts_wrapper}>
            <Card className={classes.contacts}>
                <CardContent>
                    <List component="nav" aria-labelledby="nested-list-subheader">

                        <Link className={classes.link} color="secondary"
                            href="https://www.linkedin.com/in/amngomes">
                            <ListItem button>
                                <ListItemIcon>
                                    <LinkedInIcon />
                                </ListItemIcon>
                                <ListItemText primary="LinkedIn" />

                            </ListItem>
                        </Link>


                        <Link className={classes.link} color="secondary"
                            href="https://github.com/amng">
                            <ListItem button>
                                <ListItemIcon>
                                    <GitHubIcon />
                                </ListItemIcon>
                                <ListItemText primary="GitHub" />
                            </ListItem>
                        </Link>

                        <Link className={classes.link} color="secondary"
                            href="mailto:arturngomes@gmail.com">
                            <ListItem button>
                                <ListItemIcon>
                                    <AlternateEmailIcon />
                                </ListItemIcon>
                                <ListItemText color="secondary" primary="arturngomes@gmail.com" />
                            </ListItem>
                        </Link>

                    </List>
                </CardContent>
            </Card>
        </div >
    );
}
