import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AnimationIcon from './components/AnimatedIcon';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    contacts: {
        margin: "auto",
    },
    link:
    {

    },
    page_not_found_wrapper: {
        position: "absolute",
        width: "100%",
        height: "calc(100vh - 24px)",
        marginTop: "-48px",
        display: "flex",
        backgroundColor: theme.palette.background.default,
    },
    internal_wrapper:
    {
        margin: "auto"
    },
    icon: {

    }
}));

export default function NotFound() {
    const classes = useStyles();

    return (
        <div className={classes.page_not_found_wrapper}>
            <div className={classes.internal_wrapper}>
                <AnimationIcon src={process.env.PUBLIC_URL + "/404.svg"} className={classes.icon}></AnimationIcon>
                <Typography color="textSecondary" gutterBottom variant="h4" component="div" align="justify">
                    Page Not Found                    
                </Typography>
            </div>
        </div >
    );
}
