import React from 'react';
import { Typography, makeStyles, Grid, LinearProgress } from '@material-ui/core';

const useStyles = makeStyles({
    progress: {
        height: "100%",
        width: "100px",
        margin: "0px",
    },
    container:
    {
        display: "flex",
        whiteSpace: "nowrap",
    },
    competence: {
        margin: "0px",
    },
    competence_time:{
        position: "absolute",
        right: "12px",
        top: 0,
        bottom: 0
    },
    progress_container: {
        marginRight: "12px",
        position: "relative",
    },
});

export default function CompetenceEntry(props: any) {
    const classes = useStyles();

    const percent = function()
    {
        return (props.value / props.max)*100;
    }

    const experienceText = function()
    {
        if ("experienceText" in props)
        {
            return props.experienceText;
        }
        if (props.value === 1)
            return "1 year";
        return props.value + " years";
    }

    return (
            <Grid item md={4} sm={6} xs={12}>
                <div className={classes.container}>
                <div  className={classes.progress_container}>
                    <LinearProgress className={classes.progress}  variant="determinate" value={percent()}></LinearProgress>
                    <Typography className={classes.competence_time} gutterBottom variant="body1" component="div">
                        {experienceText()}
                    </Typography>
                </div>
                <Typography className={classes.competence} gutterBottom variant="body1" component="div">
                    {props.children}
                </Typography>
                </div>
            </Grid>
    );
}



